// 全域 css 設定 
*,*::before,*::after{
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;  
    line-height: 1.5; /* 16 *1.5=36px */   
    font-size: 16px;         
}

img{
    max-width: 100%;
    height: auto;
    vertical-align: middle; //display: block;
}

.container{
    max-width: 1004px;
